import { NextResponse } from 'next/server';
import { generateCourseRecommendations } from '@/lib/chatgpt';
import { myTakenCourses, courses } from '@/lib/cs-courses';

export async function POST(request: Request) {



    const courseHistory = myTakenCourses.map(course => courses[course].description);
    try {
        const body = await request.json();
        const { question } = body;
        const response = await generateCourseRecommendations({
            name: "Alex Savard",
            major: "Computer Science",
            degree: "Bachelor's",
            courseHistory: courseHistory,
            question: question
        });

        return NextResponse.json({ response });
    } catch (error) {
        console.log(error);
        return NextResponse.json(
            { error: 'Failed to process request' },
            { status: 500 }
        );
    }
} 